import React, { useState } from 'react'
import {Link} from 'react-router-dom'
import logo from '../../assets/images/logo.gif'
import android from '../../assets/logos/android.png'
import './navbar.css' 

function Navbar() {
    const downloadHandle = () => {
        window.location.href = 'https://www.google.com';
    };
    
    return (
        <>
            <nav className="navbar">
                <div className="navbar-cont" >
                    <div className="nav-logo">
                        <div className="right">
                            <Link to="/" className="navbar-logo">
                                <img src={logo} alt="" height="150px" width="150px"/>
                            </Link>
                            <div className="title">
                                DROPSHIP LITE
                            </div>
                        </div>
                    </div>
                    <div className="nav-menu">
                        <ul className="nav-list">
                            <li className="nav-item">
                                <Link to="/" className="nav-links">
                                    Home
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/donate" className="nav-links">
                                    Donate
                                </Link>
                            </li>
                            <li className="nav-item">
                                <div className="left">
                                    <div className="btn-cont btn">
                                        <button className="download btn" onClick={downloadHandle}> 
                                            <div className="btn-items">
                                                <img src={android} alt="" srcset="" className="android"/> 
                                            </div>
                                            <div className="btn-items">
                                                Download
                                            </div>
                                        </button>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </>
    )
}

export default Navbar
